import * as actionType from './actionType'
import { getData } from 'util'
import {
  API_GENERAL_BOMSETTING_DETS_PAGE,
  API_PRODUCTIONCODE_DET,
  API_GENERAL_BOMSETTING_DETS_DETAIL,
  API_GENERAL_BOMSETTING_DETS_CREATE,
  API_GENERAL_BOMSETTING_DETS_UPDATE
} from 'api'
//同步
export const update = object => ({
  type: actionType.GENERAL_BOMSETTING_DET_UPDATE,
  data: object
})

//异步
// 获取列表
export const handleGetList = reqData => dispatch => {
  dispatch({
    type: actionType.GENERAL_BOMSETTING_DET_UPDATE,
    data: {
      tableLoading: true
    }
  })
  const { searchParams = {} } = reqData
  for (let i in searchParams) {
    if (Object.prototype.toString.call(searchParams[i]) === '[object Object]') {
      searchParams[i] = searchParams[i].value
    }
  }
  getData({
    api: API_GENERAL_BOMSETTING_DETS_PAGE,
    ...reqData
  })
    .then(data => {
      // 相应的逻辑判断
      let { page, size } = reqData.searchParams
      dispatch({
        type: actionType.GENERAL_BOMSETTING_DET_UPDATE,
        data: {
          page,
          size,
          tableLoading: false,
          total: data.result.total,
          searchResult: data.result.list || []
        }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.GENERAL_BOMSETTING_DET_FAIL
      })
    })
}

// 创建/编辑
export const handleAddOrEdit = (reqData, callBack) => dispatch => {
  dispatch({
    type: actionType.GENERAL_BOMSETTING_DET_UPDATE,
    data: {
      modalTuttonLoading: true
    }
  })
  let api =
    reqData.data && reqData.data.id
      ? API_GENERAL_BOMSETTING_DETS_UPDATE
      : API_GENERAL_BOMSETTING_DETS_CREATE
  getData({
    api,
    ...reqData
  })
    .then(data => {
      // 相应的逻辑判断
      dispatch({
        type: actionType.GENERAL_BOMSETTING_DET_UPDATE,
        data: {
          modalTuttonLoading: false,
          isShowModal: false
        }
      })
      callBack && callBack()
    })
    .catch(err => {
      dispatch({
        type: actionType.GENERAL_BOMSETTING_DET_FAIL
      })
    })
}
// 获取 - 单个BOM数据
export const getSingleDet = (reqData, callBack) => dispatch => {
  console.log('reqData is ', reqData)
  getData({
    api: API_GENERAL_BOMSETTING_DETS_DETAIL,
    ...reqData
  })
    .then(data => {
      callBack && callBack(data)
      const { result } = data
      const {
        lineCode = '',
        lineName = '',
        bomType = '',
        calcFactor = '',
        locationCode = '',
        locationName = '',
        productCode = '',
        productName = '',
        middleCategoryCode = '',
        middleCategoryName = '',
        smallCategoryCode = '',
        smallCategoryName = '',
        largeCategoryCode = '',
        largeCategoryName = '',
        spec = '',
        status = '',
        unit = '',
        bomAbilityOneList = []
      } = result
      try {
        const bomTypeValue =
          bomType == 1
            ? '通用BOM'
            : bomType == 2
            ? '分解型'
            : bomType == 3
            ? '粗加工'
            : '--'
        const statusValue = status == 1 ? '启用' : status == 0 ? '禁用' : '--'
        if (bomAbilityOneList && bomAbilityOneList.length > 0) {
          bomAbilityOneList.map((item, index, arr) => {
            const {
              productCode = '',
              valueShare = '',
              scrapRate = '',
              number = ''
            } = item
            item.productCode = {
              value: `${productCode}`,
              children: `${productCode}`
            }
            item.valueShare = String(valueShare)
            item.scrapRate = String(scrapRate)
            item.number = String(number)
          })
        }
        let generalBomList = []
        let dataNow = Date.now()
        for (let i in result) {
          if (i === 'workerFee') {  // 人工
            if (result && result.workerFee == 0) {
            // if (false) {
              console.log(result && result.workerFee == 0, 9999)
            } else { // 当不为零的时候展示
              generalBomList.push({
                craftType: { value: 'FY001', children: '人工费用' },
                id: dataNow,
                number: result && result.workerFee || '',
                unit: { value: result && result.workerFeeUnit, children: result.workerFeeUnit == '1' ? '小时' : result.workerFeeUnit == '2' ? '元' : '' }
              })
            }
          } else if (i === 'machineFee') {  // 机时
            if (result && result.machineFee == 0) {
              // if (false) {
              console.log(result && result.workerFee && result.workerFee == 0, 9999)
            } else {
              generalBomList.push({
                craftType: { value: 'FY002', children: '机器费用' },
                id: `${dataNow - 100}` || '',
                number: result && result.machineFee || '',
                unit: { value: result && result.machineFeeUnit, children: result.machineFeeUnit == '1' ? '小时' : result.machineFeeUnit == '2' ? '元' : '' }
              })
            }
          } else if (i === 'supportMaterialFee') { // 辅料
            if (result && result.supportMaterialFee == 0) {
              // if (false) {
              console.log(result && result.workerFee && result.workerFee == 0, 9999)
            } else {
              generalBomList.push({
                craftType: { value: 'FY003', children: '辅料费用' },
                id: `${dataNow - 200}` || '',
                number: result && result.supportMaterialFee || '',
                unit: { value: result && result.supportMaterialFeeUnit, children: result.supportMaterialFeeUnit == '1' ? '小时' : result.supportMaterialFeeUnit == '2' ? '元' : '' }
              })
            }
          }
        }
        let dataFormater = {
          ...result,
          calcFactor,
          productName,
          middleCategory: { value: `${`${middleCategoryCode}`}`, children: `${middleCategoryCode} ${middleCategoryName}` },
          smallCategory: { value: `${smallCategoryCode}`, children: `${smallCategoryCode} ${smallCategoryName}` },
          largeCategory: { value: `${largeCategoryCode}`, children: `${largeCategoryCode} ${largeCategoryName}` },
          spec,
          unit,
          location: {
            value: `${locationCode}`,
            children: `${locationCode} ${locationName}`
          },
          line: {
            value: `${lineCode} ${lineName}`,
            children: `${lineCode} ${lineName}`
          },
          bomType: {
            value: `${bomType}`,
            children: `${bomTypeValue}`
          },
          status: {
            value: `${status}`,
            children: `${statusValue}`
          },
          productCode: {
            value: `${productCode}`,
            children: `${productCode}`
          }
        }
        console.log(' generalBomList is', generalBomList)
        dispatch({
          type: actionType.GENERAL_BOMSETTING_DET_UPDATE,
          data: {
            modalData: dataFormater,
            bomTypeSelected: bomType,
            // tableDataSource: bomAbilityOneList
            tableDataSource: generalBomList
          }
        })
      } catch (error) {
        console.log(error)
      }
    })
    .catch(err => {
      dispatch({
        type: actionType.GENERAL_BOMSETTING_DET_FAIL
      })
    })
}
// 获取 - 单个商品数据
export const getSingleProductDet = (reqData, callBack) => dispatch => {
  console.log('reqData is ', reqData)
  getData({
    api: API_PRODUCTIONCODE_DET,
    ...reqData
  })
    .then(data => {
      console.log('res data is', data)
      const { result } = data
      let dataFormater = {
        ...result
      }
      callBack && callBack(dataFormater)
    })
    .catch(err => {
      dispatch({
        type: actionType.GENERAL_BOMSETTING_DET_FAIL
      })
    })
}



