import React, { PureComponent } from 'react'
import {
  message,
  Button
} from 'antd'
import style from './style/Tool.scss'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { delINArray } from 'util'
@connect(
  state => ({ ...state.generalBomSettingDet }),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {
    }
  }
  // 新增
  handleAdd = () => {
    const { update, tableDataSource = [] } = this.props
    const newData =
      {
        id: Date.now(),
        craftType: '',
        unit: '',
        number: ''
      }
    let tableData = tableDataSource && Array.isArray(tableDataSource) && tableDataSource.length > 0 ? tableDataSource.concat(newData) : [newData]
    update({ tableDataSource: tableData })
  }
  // 删除
  handleDel = () => {
    const { update, selectedRowKeys, tableDataSource = [], form } = this.props
    if (selectedRowKeys && selectedRowKeys.length > 0) {
      // 将form表单中的数据清除掉
      const list = form.getFieldValue('generalBomList')
      let formArr = delINArray(selectedRowKeys, list)
      console.log('formArr is', formArr)
      formArr && formArr.length > 0 && formArr.map((item, index, arr) => {
        const { number, unit, craftType, id = '' } = item
        let obj = {
          [`generalBomList[${index}][number]`]: number,
          [`generalBomList[${index}][unit]`]: unit,
          [`generalBomList[${index}][craftType]`]: craftType,
          [`generalBomList[${index}][id]`]: id
        }
        form.setFieldsValue(obj)
      })
    // 将form表单中的数据清除掉
      update(
        {
          tableDataSource: delINArray(selectedRowKeys, tableDataSource),
          selectedRowKeys: []
        })
    } else {
      return message.error('没有可以操作的选项')
    }
  }
  render() {
    return (
      <div className={style.table_tool_det}>
        <span onClick={this.handleAdd} className={style.table_tool_det_box}>
          <Button type="primary" style={{ margin: 0 }}>新增</Button>
        </span>
        <span onClick={this.handleDel}>
          <Button type="primary" style={{ cursor: 'pointer', margin: 0 }}>删除</Button>
        </span>
      </div>
    )
  }
}



